.tags {
  margin-top: 20px;
  padding: 0 32px;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f7f7f7;
  border-radius: 4px;
  box-sizing: border-box;
  @media screen and (max-width: 1050px) {
    display: none;
  }

  &__group {
    display: flex;
    align-items: center;
    gap: 40px;
  }

  &__txt {
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
  }

  &__links {
    display: flex;
    gap: 35px;
    a {
      color: rgb(0, 0, 0);
      text-decoration: none;
      font-weight: 500;
      color: #4cb649;
      letter-spacing: 0.8px;
      font-size: 14px;
      transition: all 0.1s;
      &:hover {
        transform: scale(1.05);
      }
      &:active {
        transform: scale(0.99);
      }
    }
  }
  &__other-links {
    display: flex;
    gap: 35px;
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
    a {
      text-decoration: none;
      color: #323642;
      transition: all 0.2s;
      &:hover {
        color: #4cb649;
      }
    }
  }
}
